<template lang="html">
  <div class="_full_router fundQryBody">
    <mt-header :title="title" class="cust-header" style="background:#ef4f4f;height:7%" >
      <mt-button icon="back" @click="goBack" slot="left">返回</mt-button>
      <mt-button icon="search" @click="goSearch" slot="right">查询</mt-button>
    </mt-header>
    <div style="height:93%;overflow:scroll">
      <ul class="my-hold-container"
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="10"
        infinite-scroll-immediate-check="false">
         <li>

              <div>交易日期</div>
              <div class="text-right">流水序号</div>
              <div class="text-right">平仓盈亏</div>
               <!-- <div class="text-right">平仓盈亏</div> -->
              <div class="text-right">计提金额</div>
              <!-- <div class="text-right">类型</div> -->
              <!-- <div class="text-right">备注</div> -->
            </li>
          <li style="height:45px" v-for="(item,index) in cltFundList">
        <!--    <router-link :to="{path:'/financial/detail',query:{issNo:item.issNo,stkBiz:stkBizs.subscribe}}"> -->
                 <div><div> {{item.settDate | datetext}}</div></div>
                 <div class="text-right"><div>{{item.sno}}</div></div>
                 <div class="text-right"><div>{{item.profitAmt | toMoney}}</div></div>
                 <div class="text-right"><div>{{item.accruedAmt | toMoney}}</div></div>
                
          </li>
        </ul>
    </div>
    <mt-popup
      style="width:100%"
      v-model="searchVis"
      position="top"
      >
      <div class="searchBody">
        <mt-header :title="title" class="cust-header" style="background:#ef4f4f" >
          <mt-button icon="back" @click="goBack1" slot="left">返回</mt-button>
          <mt-button  @click="goSearch1" slot="right">清空</mt-button>
        </mt-header>
        <mt-field label="起始日期" placeholder="选择起始日期"  v-model="startDateData" v-lfoucs="startDateChoose"></mt-field>
        <mt-field label="结束日期" placeholder="选择结束日期"  v-model="endDateData"  v-lfoucs="endDateChoose"></mt-field>
        <mt-button  type="danger" size="large" class="search" @click="upCltLogFundQry">立即查询</mt-button>
      </div>

    </mt-popup>

    <mt-datetime-picker
      ref="picker"
      v-model="value1"
      type="date"
      year-format="{value}年"
      month-format="{value}月"
      date-format="{value}日"
      :startDate=startDate
      :endDate=endDate
      @confirm="comDate">

    </mt-datetime-picker>

  </div>
</template>

<script>
import custHeader from  'components/custHeader'
import cookieUtil from 'js-cookie'
import {MessageBox,Cell,Header,DatetimePicker,Toast} from 'mint-ui'
Vue.component(Header.name,Header);
Vue.component(Cell.name,Cell);
Vue.component(DatetimePicker.name,DatetimePicker);
export default{
    data(){
        return {
            title:'计提购物金明细',
            searchVis:false,
            dateVis:false,
            value1:null,
            value:null,
            isStart:true,
            startDate:'',
            endDate:'',
            cltFundList:[],
            startDate:new Date("2017-1-1"),
            endDate:new Date(),
            startDateData:'',
            endDateData:'',
            loading: false,
            pars:{
                order:"desc",
      					limit:global.pageSize,
      					offset:0,
                bgnDate:"",
                endDate:""
              }

        }
    },
    created:function(){
      this.upCltLogFundQry();
    },
    components:{
      custHeader
    },
    methods:{
      loadMore:function() {
  	      let self = this;
  	      this.loading = true;
  	      this.pars.offset  +=global.pageSize;
  	      axiosUtil.upLogFundAccruedQry(this.pars).then(data=> {
  	        var data = data.data;

  	        this.cltFundList = this.cltFundList.concat(data.rows);

  	        if (data.total == this.cltFundList.length) {
  	          this.loading = true;
  	          Toast({
  	            message: "已加载最后一条",
  	            position: "bottom",
  	            duration: 1000
  	          });
      		  }else{
      			    this.loading = false;
      		  }

  	      })
  	  },
      goBack:function(){
        router.go(-1)
      },
      goBack1:function(){
        this.searchVis = false
      },
      goSearch1:function(){
        // this.searchVis = false
        this.startDateData = ""
        this.endDateData = ''
      },
      goSearch:function(){
        this.searchVis = true
      },
      startDateChoose:function(){
        // this.dateVis = true
        this.isStart = true
        this.$refs["picker"].open()
      },
      endDateChoose:function(){
        this.isStart = false
        // this.dateVis = true
        this.$refs["picker"].open()
      },
      comDate:function(value){
        if(this.isStart){
          this.startDateData = value.Format('yyyy-MM-dd')
        }else{
          this.endDateData = value.Format('yyyy-MM-dd')
        }

      },
      upCltLogFundQry:function(){
        this.searchVis = false;
        var endDate = this.endDateData?this.endDateData.substring(0,4)+this.endDateData.substring(5,7)+this.endDateData.substring(8):""
        var bgnDate = this.startDateData?this.startDateData.substring(0,4)+this.startDateData.substring(5,7)+this.startDateData.substring(8):endDate?endDate:""
        this.pars.bgnDate = bgnDate
        this.pars.endDate = endDate
        console.log(endDate,bgnDate)
        this.pars.offset = 0;
        var self = this;
    		axiosUtil.upLogFundAccruedQry(this.pars).then(data=>{
    			var data = data.data;
    			self.cltFundList=data.rows;
    		})
  		},
    }
}
</script>

<style lang="css" scoped>
.my-hold-container{
  background: #fff;
  margin-top: 12px;
  width: 100vw;
}
</style>



